import { toArray } from './utils'


const makeBundle = (arr, i, times) => {
  let bundleStr = []

  for (let c=0; c<times; c++) (
    bundleStr.push((arr[i+c] || ''))
  )

  bundleStr = bundleStr.join('')
  return bundleStr
}
const frequencyCheck = (strs, frequency) => (
  strs.map(str => {
    const times = frequency

    for (let i=0; i<times; i++) {
      const reverted = str.split('').reverse()
      let newArr = []

      reverted.forEach((char, i1) => {
        const bundle = makeBundle(reverted, i1, times);
        const future = makeBundle(reverted, i1 + times, times);

        if (bundle !== future) newArr.push(char)
      })
      str = newArr.reverse().join('')
    }

    return str.replace(/(.)\1{3,}/g, '$1$1$1')
  })
)

const Spam = () => {
  let enabled = false
  let frequency = 3

  const enable = () => enabled = true
  const disable = () => enabled = false
  const status = () => enabled
  const setFrequency = f => frequency = f
  const getFrequency = () => frequency
  const run = (strs=[]) => {
    if (!enabled || !frequency) return strs

    strs = toArray(strs)
    const checked = frequencyCheck(strs, frequency)

    return checked
  }

  return {
    enable,
    disable,
    status,
    setFrequency,
    getFrequency,
    run,
  }
}


export default Spam

