export const ENV = typeof process === 'undefined' ? 'browser' : 'server'

export const arrRemove = (arr, item) => {
  while (arr.includes(item)) {
    let index = arr.indexOf(item)
    arr = arr.splice(index, 1)
  }
  return arr
}

export const constructorStrs = [
  ['Array', Array.toString()],
  ['String', String.toString()],
  ['Number', Number.toString()],
  ['Object', Object.toString()],
  ['Function', Function.toString()],
]
export const whatIs = (item=null) => {
  const def = 'Null'

  if (item == null) return def

  const stringify = item.constructor.toString()
  const found = (constructorStrs.find(([ type, str ]) => str == stringify) || [])[0]
  return found || def
}

export const toArray = item => {
  const constructor = whatIs(item)
  return (
    constructor == 'Array' ? item :
    constructor == 'Number' || constructor == 'String'? [item] :
    null
  )
}

export const randomRange = (min=0, max=101) => (
  Math.floor(Math.random() * (max - min) + min)
)

export const logger = (...args) => (
  console.log('Profam:', ...args)
)

export const escapeSymbols = str => (
  str.replace(/[.*+?^${}()|[\]\\]/g, "\\$&")
)



